// --- Local Storage Helper Functions ---
/**
 * Loads data from localStorage for a given key.
 * @param {string} key - The key for the localStorage item.
 * @param {any} defaultValue - The value to return if the key is not found or parsing fails.
 * @returns {any} The parsed data or defaultValue.
 */
const loadFromLocalStorage = (key, defaultValue) => {
    try {
        const item = localStorage.getItem(key);
        return item ? JSON.parse(item) : defaultValue;
    } catch (error) {
        console.error(`Error loading from localStorage for key "${key}":`, error);
        return defaultValue;
    }
};

/**
 * Saves data to localStorage for a given key.
 * @param {string} key - The key for the localStorage item.
 * @param {any} data - The data to store.
*/
const saveToLocalStorage = (key, data) => {
    try {
        localStorage.setItem(key, JSON.stringify(data));
    } catch (error) {
        console.error(`Error saving to localStorage for key "${key}":`, error);
    }
};


