/**
 * Generates a reasonably unique ID string.
 * This function replaces crypto.randomUUID() for broader browser compatibility.
 * @returns {string} A unique ID string.
 */
const generateUniqueId = () => {
    const timestamp = Date.now().toString(36);
    const randomPart = Math.random().toString(36).substring(2, 15);
    return `${timestamp}-${randomPart}`;
};


// Formats a numerical value as USD currency (e.g., $1,234.56). Returns 'N/A' for invalid inputs.
const formatCurrency = (value) => {
    if (value === null || value === undefined || isNaN(value)) {
        return 'N/A';
    }
    return new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
    }).format(value);
};

/**
 * Calculates the percentage change of a dividend trend line using linear regression.
 * @param {Array} historicalDividends - An array of dividend objects with 'date' and 'dividend' properties.
 * @returns {number|null} The percentage change of the trend line, or null if not calculable.
 */
const calculateDividendTrendPercentage = (historicalDividends) => {
    // A trend line requires at least two data points to calculate a slope.
    if (!historicalDividends || historicalDividends.length < 2) {
        return null;
    }

    // This is the corrected code
    const parsedData = historicalDividends.map(d => ({
        date: new Date(d.date),
        dividend: +d.dividend
    })).sort((a, b) => a.date - b.date);

    // Perform linear regression (logic adapted from HistoricalDividendsChart)
    const n = parsedData.length;
    const xSeries = d3.range(n); // Use indices for x-values
    const ySeries = parsedData.map(d => d.dividend);
    const sumX = d3.sum(xSeries);
    const sumY = d3.sum(ySeries);
    const sumXY = d3.sum(xSeries, (d, i) => d * ySeries[i]);
    const sumX2 = d3.sum(xSeries, d => d * d);

    const slope = (n * sumXY - sumX * sumY) / (n * sumX2 - sumX * sumX);
    const intercept = (sumY - slope * sumX) / n;

    // The start and end values of the trend line
    const startValue = intercept; // At x=0
    const endValue = slope * (n - 1) + intercept; // At x=n-1

    if (startValue === 0 || isNaN(startValue) || isNaN(endValue)) {
        return null; // Cannot calculate percentage change if the starting point is zero or values are invalid
    }

    const percentageChange = ((endValue - startValue) / startValue);
    return percentageChange;
};




// Advanced Price Projection using Linear Regression and Confidence Intervals
const calculatePriceProjection = (historicalPrices, monthsToProject, currentPrice) => {
    if (!historicalPrices || historicalPrices.length < 2) {
        return { estimate: currentPrice, lowerBound: null, upperBound: null };
    }


    const sortedPrices = [...historicalPrices].sort((a, b) => new Date(a.date) - new Date(b.date));
    const lastKnownPrice = sortedPrices[sortedPrices.length - 1].close;


    const regressionData = sortedPrices.map((d, i) => ({ x: i, y: d.close }));
    const n = regressionData.length;
    let sumX = 0, sumY = 0, sumXY = 0, sumX2 = 0;
    for (const d of regressionData) {
        sumX += d.x;
        sumY += d.y;
        sumXY += d.x * d.y;
        sumX2 += d.x * d.x;
    }
    const slope = (n * sumXY - sumX * sumY) / (n * sumX2 - sumX * sumX);
    const daysToProject = monthsToProject * 30.44;
    const estimatedPrice = lastKnownPrice + (slope * daysToProject);


    const dailyChanges = [];
    for (let i = 1; i < sortedPrices.length; i++) {
        dailyChanges.push(sortedPrices[i].close - sortedPrices[i - 1].close);
    }
    const stdDev = d3.deviation(dailyChanges) || 0;
    const projectedVolatility = stdDev * Math.sqrt(daysToProject);
    const lowerBound = estimatedPrice - (1.96 * projectedVolatility);
    const upperBound = estimatedPrice + (1.96 * projectedVolatility);




    return {
        estimate: estimatedPrice > 0 ? Number(estimatedPrice.toFixed(2)) : lastKnownPrice,
        lowerBound: lowerBound > 0 ? Number(lowerBound.toFixed(2)) : null,
        upperBound: upperBound > 0 ? Number(upperBound.toFixed(2)) : null,
    };
};

/**
 * Calculates a price projection using an Exponential Decay model based on EMA NAV decay method.
 * Fits an exponential curve to historical prices for better modeling of NAV erosion in ETFs like SVOL.
 * @param {Array} historicalPrices - Array of objects with 'date' and 'close' properties (assumed monthly ends, sorted).
 * @param {number} monthsToProject - The number of months to project into the future.
 * @param {number} currentPrice - The current price of the stock (used as fallback).
 * @returns {object} An object containing the estimated price and confidence bounds.
 */
const calculatePriceProjection_EMA = (historicalPrices, monthsToProject, currentPrice) => {
    // Require at least 12 data points for a meaningful fit (e.g., 1 year of monthly data).
    if (!historicalPrices || historicalPrices.length < 12) {
        return { estimate: currentPrice, lowerBound: null, upperBound: null };
    }
    const sortedPrices = [...historicalPrices].sort((a, b) => new Date(a.date) - new Date(b.date));
    const closes = sortedPrices.map(p => p.close);
    const n = closes.length;
    // Assume times are monthly indices (0 to n-1); for daily, could compute days from dates.
    let sum_t = 0, sum_ln = 0, sum_t_ln = 0, sum_t2 = 0;
    const ln_closes = [];
    for (let i = 0; i < n; i++) {
        const t = i; // Monthly time index.
        const ln = Math.log(closes[i]);
        ln_closes.push(ln);
        sum_t += t;
        sum_ln += ln;
        sum_t_ln += t * ln;
        sum_t2 += t * t;
    }
    // Least-squares fit: b = slope (monthly log decay rate), a = intercept.
    const denominator = (n * sum_t2 - sum_t * sum_t);
    if (denominator === 0) {
        return { estimate: currentPrice, lowerBound: null, upperBound: null };
    }
    const b = (n * sum_t_ln - sum_t * sum_ln) / denominator;
    const a = (sum_ln - b * sum_t) / n;
    // Project to future time (last time is n-1, add monthsToProject).
    const projected_time = (n - 1) + monthsToProject;
    const projected_ln = a + b * projected_time;
    const estimatedPrice = Math.exp(projected_ln);
    // Calculate residuals for volatility on log scale.
    let sum_sq_res = 0;
    for (let i = 0; i < n; i++) {
        const predicted_ln = a + b * i;
        const res = ln_closes[i] - predicted_ln;
        sum_sq_res += res * res;
    }
    const variance = sum_sq_res / (n - 1);
    const std = Math.sqrt(variance);
    // Projected volatility scales with sqrt(monthsToProject) for confidence interval.
    const projectedVolatility = std * Math.sqrt(monthsToProject);
    const lower_ln = projected_ln - (1.96 * projectedVolatility);
    const upper_ln = projected_ln + (1.96 * projectedVolatility);
    return {
        estimate: estimatedPrice > 0 ? Number(estimatedPrice.toFixed(2)) : currentPrice,
        lowerBound: Math.exp(lower_ln) > 0 ? Number(Math.exp(lower_ln).toFixed(2)) : null,
        upperBound: Math.exp(upper_ln) > 0 ? Number(Math.exp(upper_ln).toFixed(2)) : null,
    };
};



// Formats a numerical value as a percentage (e.g., 12.34%). Rounds to 2 decimal places.
const formatPercentage = (value) => {
    if (value === null || value === undefined || isNaN(value)) {
        return 'N/A';
    }
    return new Intl.NumberFormat('en-US', {
        style: 'percent',
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
    }).format(value); // Corrected: Removed the "/ 100"
};


// Formats a numerical value as an exact percentage (e.g., 5.75%). Does not round.
const formatExactPercentage = (value) => {
    if (value === null || value === undefined || isNaN(value)) {
        return 'N/A';
    }
    return `${(value * 100).toFixed(4).replace(/\.0+$/, '')}%`;
};




// Formats a numerical value as a decimal to a specified number of decimal places.
const formatRatio = (value, decimals = 4) => {
    if (value === null || value === undefined || isNaN(value)) {
        return 'N/A';
    }
    return value.toFixed(decimals);
};


// Formats large numbers for AUM display (e.g., $1.23B, $45.67M)
const formatLargeNumber = (value) => {
    if (value === null || value === undefined || isNaN(value)) {
        return 'N/A';
    }
    const absValue = Math.abs(value);
    if (absValue >= 1_000_000_000_000) return `${(value / 1_000_000_000_000).toFixed(2)}T`;
    if (absValue >= 1_000_000_000) return `${(value / 1_000_000_000).toFixed(2)}B`;
    if (absValue >= 1_000_000) return `${(value / 1_000_000).toFixed(2)}M`;
    if (absValue >= 1_000) return `${(value / 1_000).toFixed(2)}K`;
    return value.toFixed(2);
};


// Calculates the Unix timestamp (milliseconds UTC) representing 00:00:00 PST for the current calendar day.
const getStartOfDayPST = () => {
    const now = new Date();
    const formatter = new Intl.DateTimeFormat('en-US', {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric',
        timeZone: 'America/Los_Angeles',
        hour12: false
    });




    const parts = formatter.formatToParts(now);
    let year, month, day;
    for (const part of parts) {
        if (part.type === 'year') year = parseInt(part.value);
        if (part.type === 'month') month = parseInt(part.value);
        if (part.type === 'day') day = parseInt(part.value);
    }




    const localMidnightInPST = new Date(`${month}/${day}/${year} 00:00:00 America/Los_Angeles`);
    return localMidnightInPST.getTime();
};




// Calculates shares, dividends, dividendPerDollar, and yieldValue based on stock data
const calculateStockMetrics = (stock) => {
    let shares = null;
    let dividends = null;
    let dividendPerDollar = null;
    let yieldValue = null;




    // This logic now reads the dividendFrequency property from the stock object.
    let dividendMultiplier;
    switch (stock.dividendFrequency) {
        case 'weekly':
            dividendMultiplier = 52;
            break;
        case 'quarterly':
            dividendMultiplier = 4;
            break;
        case 'monthly':
        default: // Defaults to monthly if the frequency isn't set
            dividendMultiplier = 12;
            break;
    }




    if (stock.amountInvested > 0 && stock.price > 0) {
        shares = stock.amountInvested / stock.price;
    }
    if (shares !== null && stock.lastDividend > 0) {
        // Calculates the total annual dividend based on the correct multiplier.
        dividends = shares * stock.lastDividend * dividendMultiplier;
    }
    if (stock.amountInvested > 0 && dividends !== null) {
        dividendPerDollar = dividends / stock.amountInvested;
    }
    if (stock.price > 0 && stock.lastDividend > 0) {
        yieldValue = (stock.lastDividend * dividendMultiplier) / stock.price;
    }




    return { shares, dividends, dividendPerDollar, yieldValue };
};




// Calculates margin interest based on Robinhood's tiered rates
const calculateMarginInterest = (balance, isGoldMember) => {
    let initialBalance = balance;




    const tiers = [
        // The first $1,000 for Gold members is still free.
        // Non-Gold rate updated.
        { threshold: 0, limit: 1000, rateNonGold: 0.0675, rateGold: 0, displayRange: 'Up to $1,000' },
        // New rate for the $1k - $50k tier: Gold is 5% (0.05), Non-Gold is 6.75% (0.0675)
        { threshold: 1000, limit: 50000, rateNonGold: 0.0675, rateGold: 0.0500, displayRange: '$1,000 - $50,000' },
        // New rate for the $50k - $100k tier: Gold is 4.8% (0.048), Non-Gold is 6.55% (0.0655)
        { threshold: 50000, limit: 100000, rateNonGold: 0.0655, rateGold: 0.0480, displayRange: '$50,000 - $100,000' },
        // New rate for the $100k - $1M tier: Gold is 4.5% (0.045), Non-Gold is 6.25% (0.0625)
        { threshold: 100000, limit: 1000000, rateNonGold: 0.0625, rateGold: 0.0450, displayRange: '$100,000 - $1,000,000' },
        // New rate for the $1M - $10M tier: Gold is 4.25% (0.0425), Non-Gold is 6.00% (0.06)
        { threshold: 1000000, limit: 10000000, rateNonGold: 0.0600, rateGold: 0.0425, displayRange: '$1,000,000 - $10,000,000' },
        // New rate for the $10M - $50M tier: Gold is 4.2% (0.042), Non-Gold is 5.95% (0.0595)
        { threshold: 10000000, limit: 50000000, rateNonGold: 0.0595, rateGold: 0.0420, displayRange: '$10,000,000 - $50,000,000' },
        // New rate for balances over $50M: Gold is 3.95% (0.0395), Non-Gold is 5.70% (0.057)
        { threshold: 50000000, limit: Infinity, rateNonGold: 0.0570, rateGold: 0.0395, displayRange: 'Over $50,000,000' }
    ]




    let totalAnnualInterest = 0;
    let remainingForTierCalculation = initialBalance;
    const interestPerTier = [];


    if (initialBalance <= 0) {
        return { daily: 0, annual: 0, monthly: 0, effectiveAnnualRate: 0, interestPerTier: [] };
    }


    tiers.forEach((tier) => {
        if (remainingForTierCalculation > 0) {
            let tierRate = isGoldMember ? tier.rateGold : tier.rateNonGold;
            let amountInThisTier = 0;




            if (isGoldMember && tier.threshold === 0) {
                if (initialBalance > 0) {
                    amountInThisTier = Math.min(initialBalance, 1000);
                    interestPerTier.push({
                        range: tier.displayRange,
                        borrowedAmountInTier: amountInThisTier,
                        annualInterestInTier: 0,
                        monthlyInterestInTier: 0,
                        rate: '0%',
                        isGoldFree: true
                    });
                    remainingForTierCalculation = Math.max(0, remainingForTierCalculation - 1000);
                }
            } else {
                const currentTierMaxAmount = tier.limit - tier.threshold;
                amountInThisTier = Math.min(remainingForTierCalculation, currentTierMaxAmount);


                if (amountInThisTier > 0) {
                    const annualInterestInTier = amountInThisTier * tierRate;
                    totalAnnualInterest += annualInterestInTier;


                    interestPerTier.push({
                        range: tier.displayRange,
                        borrowedAmountInTier: amountInThisTier,
                        annualInterestInTier: annualInterestInTier,
                        monthlyInterestInTier: annualInterestInTier / 12,
                        rate: formatExactPercentage(tierRate),
                        isGoldFree: false
                    });
                    remainingForTierCalculation -= amountInThisTier;
                }
            }
        }
    });




    const dailyInterest = totalAnnualInterest / 360;
    const monthlyInterest = totalAnnualInterest / 12;
    const effectiveAnnualRate = initialBalance > 0 ? totalAnnualInterest / initialBalance : 0;


    return { daily: dailyInterest, annual: totalAnnualInterest, monthly: monthlyInterest, effectiveAnnualRate, interestPerTier };
};


// --- Reusable UI Component: Modal ---
const Modal = ({ show, title, message, onClose, children }) => {
    if (!show) {
        return null;
    }


    return (
        <div className="fixed-overlay">
            <div className="modal-content card-expressive">
                <button onClick={onClose} className="modal-close-button">
                    <XCircle size={24} />
                </button>
                <h3 className="modal-title">{title}</h3>
                {message && <p className="modal-message custom-scrollbar">{message}</p>}
                <div className="modal-body custom-scrollbar">
                    {children}
                </div>
            </div>
        </div>
    );
};


// --- D3 Chart Components (extracted to top-level) ---
const HistoricalDividendsChart = ({ data }) => {
    const svgRef = useRef();

    useEffect(() => {
        // Ensure there are at least two data points to draw a trend line
        if (!data || data.length < 2) return;
        const svg = d3.select(svgRef.current);
        const container = d3.select(svgRef.current.parentNode);
        // Clear previous chart elements before redrawing
        svg.selectAll("*").remove();

        const containerWidth = container.node().clientWidth;
        const containerHeight = container.node().clientHeight;

        // Define chart margins and dimensions
        const margin = { top: 40, right: 40, bottom: 120, left: 100 };
        const width = containerWidth - margin.left - margin.right;
        const height = containerHeight - margin.top - margin.bottom;

        // Create the main SVG group element
        const g = svg.append("g")
            .attr("transform", `translate(${margin.left},${margin.top})`);

        // Parse and sort the data
        const parsedData = data.map(d => ({
            date: new Date(d.date),
            dividend: +d.dividend
        })).sort((a, b) => a.date - b.date);

        // Set up the X scale (for dates)
        const x = d3.scaleBand()
            .rangeRound([0, width])
            .padding(0.1)
            .domain(parsedData.map(d => d.date));




        // Set up the Y scale (for dividend amounts)
        const y = d3.scaleLinear()
            .rangeRound([height, 0])
            .domain([0, d3.max(parsedData, d => d.dividend) * 1.1]);




        // Render the X axis
        g.append("g")
            .attr("class", "axis axis--x")
            .attr("transform", `translate(0,${height})`)
            .call(d3.axisBottom(x).tickFormat(d3.timeFormat("%b %Y"))
                .tickValues(x.domain().filter((d, i) => !(i % Math.ceil(parsedData.length / 5)))))
            .selectAll("text")
            .attr("transform", "rotate(-45)")
            .style("text-anchor", "end")
            .attr("dx", "-.8em")
            .attr("dy", ".15em")
            .style("font-size", "12px");




        // Render the Y axis
        g.append("g")
            .attr("class", "axis axis--y")
            .call(d3.axisLeft(y).ticks(5).tickFormat(formatCurrency))
            .append("text")
            .attr("fill", "#000")
            .attr("transform", "rotate(-90)")
            .attr("y", -60)
            .attr("x", - (height / 2))
            .attr("dy", "0.71em")
            .style("text-anchor", "middle")
            .text("Dividend ($)")
            .style("font-size", "14px");




        // Render the bars for the bar chart
        g.selectAll(".bar")
            .data(parsedData)
            .enter().append("rect")
            .attr("class", "bar-chart-bar")
            .attr("x", d => x(d.date))
            .attr("y", d => y(d.dividend))
            .attr("width", x.bandwidth())
            .attr("height", d => height - y(d.dividend));




        // --- START: Trend Line Calculation ---
        // Perform linear regression to find the line of best fit.
        const n = parsedData.length;
        const xSeries = d3.range(n); // Use indices as the x-values for regression
        const ySeries = parsedData.map(d => d.dividend);




        const sumX = d3.sum(xSeries);
        const sumY = d3.sum(ySeries);
        const sumXY = d3.sum(xSeries, (d, i) => d * ySeries[i]);
        const sumX2 = d3.sum(xSeries, d => d * d);




        // Calculate the slope (m) and y-intercept (b) of the trend line
        const slope = (n * sumXY - sumX * sumY) / (n * sumX2 - sumX * sumX);
        const intercept = (sumY - slope * sumX) / n;




        // Determine the start and end points of the trend line based on the regression
        const trendStart = { x: 0, y: slope * 0 + intercept };
        const trendEnd = { x: n - 1, y: slope * (n - 1) + intercept };
        // --- END: Trend Line Calculation ---




        // --- START: Draw Trend Line ---
        // Append a line element to the SVG for the trend line
        g.append("line")
            .attr("class", "trend-line")
            // The x-coordinates are the center of the first and last bars
            .attr("x1", x(parsedData[trendStart.x].date) + x.bandwidth() / 2)
            .attr("y1", y(trendStart.y))
            .attr("x2", x(parsedData[trendEnd.x].date) + x.bandwidth() / 2)
            .attr("y2", y(trendEnd.y))
            // Style the trend line to be visible
            .attr("stroke", "var(--color-red-500)")
            .attr("stroke-width", 2)
            .attr("stroke-dasharray", "5,5"); // Dashed line for visual distinction
        // --- END: Draw Trend Line ---




    }, [data]);




    return <svg ref={svgRef} id="historical-dividend-chart" className="chart-svg-full"></svg>;
};

