    const handleTickerEnter = useCallback(async (stockId, tickerValue, tabType, forceRefresh = false) => {
        if (!isAuthReady || !userId) {
            setModalTitle('App Not Ready');
            setModalMessage('Please wait for the app to finish loading.');
            setShowModal(true);
            return;
        }
        const normalizedTicker = tickerValue.toUpperCase().trim();

        const currentStocks = tabType === 'select' ? [...selectStocks] : [...monthlyStocks];
        const stockIndex = currentStocks.findIndex(s => s.id === stockId);

        if (stockIndex === -1) return;

        const existingStock = currentStocks[stockIndex];

        if (existingStock.ticker === normalizedTicker && !forceRefresh && normalizedTicker !== '') {
            return;
        }

        if (!normalizedTicker) {
            const clearedStock = {
                ...existingStock,
                ticker: '',
                amountInvested: null,
                price: null,
                lastDividend: null,
                companyName: '',
                historicalDividends: [],
                historicalPrices: [],
                marketCap: null,
                yearHigh: null,
                yearLow: null,
                isQuarterlyDividendsOn: false,
            };
            currentStocks[stockIndex] = clearedStock;
            updateStocksInLocalStorage(tabType, currentStocks);
            return;
        }

        // *** FIX IS HERE: Changed fetchFmpData to fetchStockData ***
        const fetchedData = await fetchStockData(normalizedTicker);

        const updatedStock = {
            ...existingStock,
            ticker: normalizedTicker,
            price: fetchedData.price,
            lastDividend: fetchedData.lastDividend,
            companyName: fetchedData.companyName,
            historicalDividends: fetchedData.historicalDividends || [],
            historicalPrices: fetchedData.historicalPrices || [],
            marketCap: fetchedData.marketCap || null,
            yearHigh: fetchedData.yearHigh || null,
            yearLow: fetchedData.yearLow || null,
        };

        currentStocks[stockIndex] = updatedStock;
        updateStocksInLocalStorage(tabType, currentStocks);
    }, [isAuthReady, userId, fetchStockData, selectStocks, monthlyStocks, updateStocksInLocalStorage]);































